import './App.css';
import React, { useEffect, useState } from 'react';
import customAxios from './customAxios';

function App() {
  const [ip, setIp] = useState('');

  function callback(data){
    setIp(data);
  }

  useEffect(
    () => {
      customAxios('/myip',callback);
    },[]
  );
  return (
    <div className="App">
      <header className="App-header">
        Current Ip Address is {ip}
      </header>
    </div>
  );
}

export default App;
